import { Component, OnInit, Inject } from '@angular/core';
import { RoleModel } from '../../../../../shared/models/role.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { ThemTaiKhoanService } from './them-tai-khoan.service';
import { AlertDialogComponent } from '../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { UserModel } from '../../../../../shared/models/user.model';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { DatePipe } from '@angular/common';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-them-tai-khoan',
  templateUrl: './them-tai-khoan.component.html',
  styleUrls: ['./them-tai-khoan.component.scss'],
})
export class ThemTaiKhoanComponent implements OnInit {
  _user = new UserModel('', '', '', '', '', '', '');
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogMainRef: MatDialogRef<ThemTaiKhoanComponent>,
    private __service: ThemTaiKhoanService,
    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar,
    private __router: Router,
    public datepipe: DatePipe
  ) {}
  _title = 'Thêm tài khoản';
  showProgressBar = false;
  _titleMainButton = 'Thêm';
  _roles: RoleModel[];
  ngOnInit(): void {
    if (this.data)
      if (this.data.thietBi) {
        this._title = 'Cập nhật';
        this._titleMainButton = 'Cập nhật';
        this._user = this.data.thietBi;
      }
    if (this.data.roles) {
      this._roles = this.data.roles;
    }
  }

  add() {
    if (
      this._user.Username == '' ||
      this._user.Password == '' ||
      this._user.PasswordConfirm == '' ||
      this._user.DisplayName == '' ||
      this._user.RoleId == ''
    ) {
      this.__snackbar.open('Vui lòng nhập đủ thông tin', null, {
        duration: 2000,
      });
    } else if (this._user.Password != this._user.PasswordConfirm) {
      this.__snackbar.open('Mật khẩu không khớp', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;
      this.__service.addUser(this._user).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open('Cập nhật thành công', null, {
              duration: 2000,
            });
            this.dialogMainRef.close({
              data: this._user,
              result: true,
            });
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
        },
        (error) => {
          this.dialogMainRef.close({
            result: false,
          });
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    }
  }
  addEventNgaySinhChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this._user.Birthday = this.datepipe.transform(event.value, 'yyyy-MM-dd');
  }
  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._user,
          result: false,
        });
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
}
