import { Component, OnInit, ViewChild } from '@angular/core';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { MatDialog } from '@angular/material/dialog';
// import { CapNhatTaiKhoanComponent } from './cap-nhat-tai-khoan/cap-nhat-tai-khoan.component';
import { ThemTaiKhoanComponent } from './them-tai-khoan/them-tai-khoan.component';
import { UserModel } from '../../../../shared/models/user.model';
import { RoleModel } from '../../../../shared/models/role.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { QuanLyTaiKhoanService } from './quan-ly-tai-khoan.service';

import { MatSnackBar } from '@angular/material/snack-bar';
import { AlertDialogComponent } from 'src/app/shared/pages/alert-dialog/alert-dialog.component';
import { CapNhatTaiKhoanComponent } from './cap-nhat-tai-khoan/cap-nhat-tai-khoan.component';
import { DoiMatKhauComponent } from 'src/app/shared/pages/doi-mat-khau/doi-mat-khau.component';
import { environment, eMenuAdmin } from 'src/environments/environment';
import { MainAdminComponent } from '../main-nav.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-quan-ly-tai-khoan',
  templateUrl: './quan-ly-tai-khoan.component.html',
  styleUrls: ['./quan-ly-tai-khoan.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition(
        'expanded <=> collapsed',
        animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')
      ),
    ]),
  ],
})
export class QuanLyTaiKhoanComponent implements OnInit {
  _length = 0;
  __user: UserModel;
  displayedColumns = [
    'UserId',
    'Username',
    'DisplayName',
    'RoleName',
    'Status',
    'LockDate',
    'Email',
    'Birthday',
    'PhoneNumber',
    'actions',
  ];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    public __dialog: MatDialog,
    private __service: QuanLyTaiKhoanService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    private __mainComponent: MainAdminComponent
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.ADMIN,
      eMenuAdmin[eMenuAdmin.QUAN_LY_TAI_KHOAN]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.ADMIN
    );
  }
  _roles: RoleModel[];
  _users: UserModel[];
  dataSource: MatTableDataSource<UserModel>;
  showProgressBar = true;
  ngOnInit(): void {
    this.getDataRoles();
    this.getDataUser();
    this.getDataUsers();
  }
  private getDataUsers() {
    this.showProgressBar = true;

    this.__service.getAllUsers().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._users = data;
        this.dataSource = new MatTableDataSource(this._users);

        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (data: UserModel, filter: string) =>
          data.Username.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.RoleName.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.DisplayName.toLocaleLowerCase().indexOf(filter) != -1;

      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  private getDataUser() {
    //Lấy dữ liệu từ server
    this.showProgressBar = true;
    this.__service.getUser().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this.__user = data;
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  private getDataRoles() {
    this.showProgressBar = true;
    this.__service.getAllRoles().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._roles = data;
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }
  addUser() {
    const dialogRef = this.__dialog.open(ThemTaiKhoanComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        roles: this._roles,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
        const data = this.dataSource.data;

        // var thietBi = new UserModel(thietBiRes)
        // thietBi.NuocSX = this._nuocSanXuats.find(f => f.MaNuoc == thietBiRes.MaNuocSanXuat).TenNuoc

        // data.push(thietBi)
        // this.dataSource.data = data
      }
    });
  } //end add
  changePassword() {
    const dialogRef = this.__dialog.open(DoiMatKhauComponent, {
      disableClose: true,
      width: '20rem',
      data: {
        user: this.__user,
        isClient: false
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
      }
    });
  }
  edit(user: UserModel) {
    const dialogRef = this.__dialog.open(CapNhatTaiKhoanComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        roles: this._roles,
        user: user,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
        const data = this.dataSource.data;

        // var thietBi = new UserModel(thietBiRes)
        // thietBi.NuocSX = this._nuocSanXuats.find(f => f.MaNuoc == thietBiRes.MaNuocSanXuat).TenNuoc

        // data.push(thietBi)
        // this.dataSource.data = data
      }
    });
  }
  lock(user: UserModel) {
    var dialogRef = this.__dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: `Khóa tài khoản ${user.Username}/${user.DisplayName}?`,
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        //khóa
        this.showProgressBar = true;
        this.__service.lockUser(user).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.__snackbar.open(
                `Khóa tài khoản ${user.Username}/${user.DisplayName} thành công`,
                null,
                {
                  duration: 2000,
                }
              );
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.showProgressBar = false;
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }
  unLock(user: UserModel) {
    var dialogRef = this.__dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: `Mở khóa tài khoản ${user.Username}/${user.DisplayName}?`,
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        //khóa
        this.showProgressBar = true;
        this.__service.openUser(user).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.__snackbar.open(
                `Mở khóa tài khoản ${user.Username}/${user.DisplayName} thành công`,
                null,
                {
                  duration: 2000,
                }
              );
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.showProgressBar = false;
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
