import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../environments/environment';
import { UserModel } from '../../../../shared/models/user.model';
@Injectable({ providedIn: 'root' })
export class QuanLyTaiKhoanService {
  constructor(private httpClient: HttpClient) {}
  getAllUsers(): Observable<any> {
    let url = environment.api.url + 'User/getAll';
    return this.httpClient.get(url);
  }
  getUser(): Observable<any> {
    let url = environment.api.url +'User/getInfo'
    return this.httpClient.get(url)
  }
  updateUser(user: UserModel): Observable<any> {
    let url = environment.api.url + 'User/update';
    return this.httpClient.post(url, user);
  }
  getAllRoles(): Observable<any> {
    let url = environment.api.url + 'Role/getAll';
    return this.httpClient.get(url);
  }
  lockUser(user: UserModel): Observable<any> {
    user.Status = environment.status.LOCKED
    let url = environment.api.url + 'User/update';
    return this.httpClient.post(url, user);
  }
  openUser(user: UserModel): Observable<any> {
    user.Status = environment.status.OPEN
    let url = environment.api.url + 'User/update';
    return this.httpClient.post(url, user);
  }
}
